import { MdWork } from 'react-icons/md';
import { MdOutlineFamilyRestroom } from 'react-icons/md';
import { BsFillPersonFill } from 'react-icons/bs';
import { BsFillBuildingFill } from 'react-icons/bs';
import { BsVectorPen } from 'react-icons/bs';
import { HiOutlineLibrary } from 'react-icons/hi';

// -------------------------------[ FEACTURES SECTION ]------------------------------- //

export default function Feactures() {
    return (
        <div className="feactures-section">
            <div className="container">
                <div className="row align-items-center breakpoint-sm">
                    <div className="col-12 col-sm-6">
                        <div className="vstack gap-3">
                            <div className="row g-3 ">
                                <div className="col-12 col-lg-6 ">
                                    <div className="card" style={{ height: '100%', borderColor: '#ea7a69' }}>
                                        <div className="card-body text-start vstack gap-2">
                                            <BsFillPersonFill size={30} color='#ea7a69' />
                                            <h5 className="card-title">Civil</h5>
                                            <p className="card-text">
                                                Juicios ordinarios, ejecutivos y especiales,
                                                indemnizaciones de perjuicios, posesiones
                                                efectivas, juicios de partición,
                                                terminación de contrato, herencias,
                                                bienes, etc.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-12 col-lg-6 ">
                                    <div className="card" style={{ height: '100%', borderColor: '#ea7a69' }}>
                                        <div className="card-body text-start vstack gap-2">
                                            <MdOutlineFamilyRestroom size={30} color='#ea7a69' />
                                            <h5 className="card-title">De Familia</h5>
                                            <p className="card-text">
                                                Divorcios en general, nulidad matrimonial, cuidado personal,
                                                relación directa y regular, alimentos,
                                                adopción, violencia intrafamiliar.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row g-3">
                                <div className="col-12 col-lg-6">
                                    <div className="card" style={{ height: '100%', borderColor: '#ea7a69' }}>
                                        <div className="card-body text-start vstack gap-2">
                                            <BsFillBuildingFill size={30} color='#ea7a69' />
                                            <h5 className="card-title">Asesoría a empresa</h5>
                                            <p className="card-text">
                                                Constitución de sociedades anónimas, sociedades por acciones, EIRL, entre otras.
                                                Asesorías laborales a empresas y sindicatos. Trámites ante SII y/o Tesorería.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-12 col-lg-6">
                                    <div className="card" style={{ height: '100%', borderColor: '#ea7a69' }}>
                                        <div className="card-body text-start vstack gap-2">
                                            <MdWork size={30} color='#ea7a69' />
                                            <h5 className="card-title">Laboral</h5>
                                            <p className="card-text">
                                                Demandas por despido injustificado, despido incausado,
                                                autodespido, tutela de derechos, revisión de finiquitos,
                                                cobro de prestaciones impagas e indemnizaciones de perjuicios.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row g-3">
                                <div className="col-12 col-lg-6">
                                    <div className="card" style={{ height: '100%', borderColor: '#ea7a69' }}>
                                        <div className="card-body text-start vstack gap-2">
                                            <BsVectorPen size={30} color='#ea7a69' />
                                            <h5 className="card-title">Escrituras</h5>
                                            <p className="card-text">
                                                Estudios de título, confección de escrituras públicas de compra-venta,
                                                usufructo, servidumbres, cesión de derechos, testamentos y contratos en general.
                                                Regularización de bienes inmuebles, inscripciones CBR. Asignación, activación y/o
                                                fusión roles SII, entre otros.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-12 col-lg-6">
                                    <div className="card" style={{ height: '100%', borderColor: '#ea7a69' }}>
                                        <div className="card-body text-start vstack gap-2">
                                            <HiOutlineLibrary size={30} color='#ea7a69' />
                                            <h5 className="card-title">Administrativo</h5>
                                            <p className="card-text">
                                                Experta en servicio público, derechos y obligaciones de funcionarios,
                                                régimen de responsabilidad, carrera funcionaria, asesoría en
                                                desvinculaciones, calificaciones, procesos disciplinarios,
                                                vulneración de derechos fundamentales, entre otros.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-12 col-sm-6 d-flex justify-content-end feactures-content" >
                        <div className="text-center feacture-text">
                            <h1 >
                                ¿Quién soy? y <span style={{ color: '#ea7a69' }}>Áreas Prácticas</span>
                            </h1>
                            <p className="text-justify lead mt-4">
                                Soy una abogada que apunta a una sociedad más justa,
                                donde imperen los valores y los derechos de cada uno, enfocada
                                a ser reconocida y destacada por el trabajo legal desempeñado
                                con profesionalismo y calidad humana. Las  áreas prácticas
                                abarcan Derecho Civil, Derecho de Familia, Asesoría y
                                Constitución de Empresas, Derecho Laboral, Derecho Registral
                                y Derecho Administrativo.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
